import React from 'react';
import { Container, Row, Col, Button } from 'react-bootstrap';
import PageTitle from '../../components/Body/PageTitle';
import Card from '../../components/Body/Card';
import Section from '../../components/Body/Section';
import StockBackend from '../../services/StockBackend/StockBackend';

class DashboardHome extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            log: ''
        }

        this.stockBackend = new StockBackend();
        this.getBackendTest = this.getBackendTest.bind(this);
    }

    getBackendTest() {
        this.stockBackend.getTest().then(response => {
            this.setState({log: response});
        });

        console.log(this.state.log);
    }

    render() {
        return(
            <Container fluid='true'>
                <PageTitle title="Dashboard" />
                <Row>
                    <Col xl={3} md={6}>
                        <Card title='Stock Property' bgStyle='bg-primary'/>
                    </Col>
                    <Col xl={3} md={6}>
                        <Card title='Stock Property' bgStyle='bg-warning'/>
                    </Col>
                    <Col xl={3} md={6}>
                        <Card title='Stock Property' bgStyle='bg-success'/>
                    </Col>
                    <Col xl={3} md={6}>
                        <Card title='Stock Property' bgStyle='bg-danger'/>
                    </Col>
                </Row>
                <Row>
                    <Col xl={6}>
                        <Section title='API Test'>
                            <Button varient='outline-danger' size='lg' onClick={this.getBackendTest}>Backend Request</Button>
                        </Section>
                    </Col>
                </Row>
            </Container>
        )
    }
}

export default DashboardHome;